import React from 'react';
import Head from 'next/head';
// import { render } from 'react-dom';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

import styled from 'styled-components';

import '../css/normalize.css';
import '../css/skeleton.css';


class Navbar extends React.Component {

	constructor(props) {
	    super(props);
	    this.scrollToTop = this.scrollToTop.bind(this);
  	}

	  componentDidMount() {

	    Events.scrollEvent.register('begin', function () {
	      console.log("begin", arguments);
	    });

	    Events.scrollEvent.register('end', function () {
	      console.log("end", arguments);
	    });

	  }
	  scrollToTop() {
	    scroll.scrollToTop();
	  }
	  scrollTo() {
	    scroller.scrollTo('scroll-to-element', {
	      duration: 800,
	      delay: 0,
	      smooth: 'easeInOutQuart'
	    })
	  }

	scrollToWithContainer() {

	    let goToContainer = new Promise((resolve, reject) => {

	    	Events.scrollEvent.register('end', () => {
	        	resolve();
	        	Events.scrollEvent.remove('end');
	      	});

	      	scroller.scrollTo('scroll-container', {
	        	duration: 800,
	        	delay: 0,
	        	smooth: 'easeInOutQuart'
	      	});
	    });

	    goToContainer.then(() =>
	    	scroller.scrollTo('scroll-container-second-element', {
	        duration: 800,
	        delay: 0,
	        smooth: 'easeInOutQuart',
	        containerId: 'scroll-container'
	    }));
	}

	componentWillUnmount() {
		Events.scrollEvent.remove('begin');
	    Events.scrollEvent.remove('end');
	}

	render () {
	    return(
		    <div className="nav">
		      <ul>
		        <li className="home"><a href="#" activeClass="active" className="test1" to="test1" spy={true} smooth={true} duration={500}>Artists</a></li>
		        <li className="tutorials"><a href="#">News</a></li>
		        <li className="about"><a href="#">Label Info</a></li>
		        <li className="news"><a href="#">Store</a></li>
		        <li className="contact"><a href="#">Contact</a></li>
		      </ul>
		    </div>
	    )
	}


}

export default Navbar;

